.bg-primary {
  background-color: var(--secondary) !important;
  border-color: #F7FAFF
}

.bg-primary:hover {
  background-color: var(--light) !important;
  color: var(--secondary) !important;
  border-color: #F7FAFF
}

.text-primary {
  color: var(--primary) !important;
}

.btn-primary {
  background-color: var(--primary) !important;
}

.btn-outline-primary {
  color: var(--primary) !important;
}

.btn-outline-primary:hover {
  color: #fff;
}

.btn-secondary {
  background-color: var(--secondary) !important;
}

